import { ThemeProvider, CssBaseline, Container, Grid, Box, Stack, TextField, Button, Typography, InputAdornment  } from '@mui/material'
import { AccountCircle, Lock } from '@mui/icons-material'

import { theme } from '../../Styles/Themes/Auth.theme'
import { style } from '../../Styles/Styles/Auth.style'

import useField from '../../Hooks/useField'

import { signin } from '../../Services/Api/Auth'
import { setSessionCookie } from '../../Services/CookieSession'

function Login() {
  const email = useField({ type: 'email' })
  const password = useField({ type: 'password' })

  const handleLogin = async (e) => {
    e.preventDefault()

    const body = {
      email: email.value,
      password: password.value
    }

    signin(body).then((res) => {
      console.log(res)
      setSessionCookie(JSON.stringify(res))
    }).catch((err) => {
      console.log(err)
    })
  }

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />

      <Container maxWidth='sm' sx={{ pt: 20 }}>
        <Grid container direction='column' justifyContent='center' alignItems='center'>
          <Grid item>
            <Box sx={style.boxForm}>
              <Typography variant='h5' textAlign='center'>Area de Cliente</Typography>
              <hr />
              <form onSubmit={handleLogin}>
                <Stack>
                  <TextField
                    label='Correo'
                    helperText='Nunca reveles tu correo'
                    variant='standard'
                    required
                    {...email}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position='start'>
                          <AccountCircle />
                        </InputAdornment>
                      )
                    }}
                  />
                  <TextField
                    label='Contraseña'
                    helperText='Nunca reveles tu contraseña'
                    variant='standard'
                    required
                    sx={{ mt: 2, mb: 2 }}
                    {...password}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position='start'>
                          <Lock />
                        </InputAdornment>
                      )
                    }}
                  />
                  <Button type='submit' variant='outlined'>Login</Button>
                </Stack>
              </form>
            </Box>
          </Grid>
        </Grid>
      </Container>
    </ThemeProvider>
  )
}

export default Login
